import { DoubleSide, Mesh, CanvasTexture, MeshBasicMaterial, PlaneGeometry, Vector3, BufferGeometry, Box3 } from "three";
import type { Color, Object3D, Scene } from "three";
import { TextGeometry } from "three-stdlib";
import { IExtMesh } from "../../interfaces";
import { hex_to_color } from "../../helpers/misc.helper";
import { MapObjData, MapRetailer } from "src/interfaces/mapApiTypes";
import { useMapBoxContext } from "src/contexts/MapBoxContextProvider";
import useMaterialAndGeometry from "./useMaterialAndGeometry";
import { useCallback } from "react";

const useDrawLogoHelper = () => {

  const { allIndexedRetailers, allIndexedMapObjects, floorsData, mapConfig } = useMapBoxContext();
  const { getRenderOrder } = useMaterialAndGeometry();

  const drawTextLogoStoreOnMap = (allNonIndexedMapObjects: Record<any, any>[], scene: Scene, textLogoNamePrefix: string, allIndexedMapObjects: Record<string, MapObjData>, allIndexedRetailers: Record<string, MapRetailer>, config: Record<any, any>, myFont: any, floors: Record<any, any>[]) => {
    for (let textLogoLayerAddedIndex = 0; textLogoLayerAddedIndex < allNonIndexedMapObjects.length; textLogoLayerAddedIndex++) {
      addTextOrLogoOnStore(allNonIndexedMapObjects[textLogoLayerAddedIndex], scene, textLogoNamePrefix, allIndexedMapObjects, allIndexedRetailers, config, myFont, floors);
    }
  }

  const addTextOrLogoOnStore = (map_obj: any, scene: Scene, textLogoNamePrefix: string, allIndexedMapObjects: Record<string, MapObjData>, allIndexedRetailers: Record<any, any>, config: Record<any, any>, myFont: any, floors: Record<any, any>[]) => {
    //@ts-ignore
    let mesh = scene.getObjectByProperty('object_id', map_obj.map_obj_name);
    if (mesh) {
      //@ts-ignore
      add_store_name_logo(mesh, textLogoNamePrefix, allIndexedMapObjects, allIndexedRetailers, config, myFont, floors, scene);
    }
  }

  const getMeshGroupBoundingBox = (mesh: Mesh | Array<Mesh>) => {
    if (!Array.isArray(mesh)) {
      mesh = [mesh];
    }
    const box = mesh.reduce((box, obj) => {
      if (!obj.geometry.boundingBox) {
        obj.geometry.computeBoundingBox();
      }
      //@ts-ignore
      box.union(obj.geometry.boundingBox);
      return box;
    }, new Box3());
    return box;
  }

  const layer_text_logo_position_by_id = (object_id: string, newMeshPos: Vector3, mesh_size: Vector3, newMesh: Mesh, allIndexedMapObjects: Record<any, any>) => {
    let map_obj = allIndexedMapObjects[object_id] as MapObjData | undefined;

    newMeshPos.z = -mesh_size.z;

    newMesh.rotateZ((map_obj?.rotate || 0) * Math.PI / 180);

    newMesh.scale.y *= -1;

    newMeshPos.x += (map_obj?.offset_x || 0);
    newMeshPos.y += (map_obj?.offset_y || 0);

    newMesh.position.set(newMeshPos.x, newMeshPos.y, newMeshPos.z + 1);
  }

  const getImage = (map_obj: Record<string, any>, retailer?: MapRetailer): HTMLImageElement | null => {
    const img = new Image();
    img.crossOrigin = 'Anonymous';
    if (map_obj.obj_type === 'retailer') {
      if (retailer) {
        img.src = retailer.logo;
      } else {
        console.error('retailer not found');
        return null;
      }
    } else if (map_obj.obj_type === 'special') {

      let svg_image_name = map_obj.value;
      if (map_obj.layer_type === 'kiosk') svg_image_name = 'kiosk';
      if (map_obj.layer_type === 'escalator') svg_image_name = 'escalator';
      if (svg_image_name) {
        let svg_color = '222222';
        if (map_obj.text_color != null) {
          svg_color = map_obj.text_color.replace('#', '');
        }

        const svg_block = document.getElementById('map-special-svg-' + svg_image_name)
        if (!svg_block) {
          console.error('svg_block not found', svg_image_name);
          return null;
        }
        const svg_element = svg_block.querySelector("svg");
        if (!svg_element) {
          console.error('svg_element not found', svg_image_name);
          return null;
        }

        const svgColoredParts = svg_element.querySelectorAll('[fill]');
        svgColoredParts.forEach((svgColoredPart) => {
          svgColoredPart.setAttribute('fill', '#' + svg_color);
        })

        const svgData = (new XMLSerializer()).serializeToString(svg_element);
        img.src = "data:image/svg+xml;base64," + window.btoa(unescape(encodeURIComponent(svgData)));
      }

    } else {
      if (map_obj.custom_image !== '' && map_obj?.custom_image?.startsWith('http')) {
        img.src = map_obj.custom_image;
      } else {
        return null;
      }
    }

    return img;
  }

  const processImage = (img: HTMLImageElement, map_obj: Record<string, any>, afterOnload: (geometry: PlaneGeometry, material: MeshBasicMaterial) => void) => {
    img.onload = function () {
      const c = document.createElement("canvas");
      const ctx = c.getContext("2d");

      c.width = img.width;
      c.height = img.height;

      ctx?.drawImage(img, 0, 0);

      let texture = new CanvasTexture(c);
      texture.colorSpace = 'srgb'

      const geometry = new PlaneGeometry(map_obj.size, map_obj.size * img.height / img.width);
      const material = new MeshBasicMaterial({
        map: texture,
        transparent: true,
        side: DoubleSide,
        depthTest: false,
        depthWrite: false,
      });

      if (map_obj.layer_type === 'kiosk') {
        material.visible = false;
      }

      afterOnload(geometry, material);
    };
  }

  const getImageLogo = useCallback((allIndexedMapObjects: Record<string, MapObjData>, allIndexedRetailers: Record<string, any>, map_obj: Record<string, any>, mesh: BufferGeometry, object_id: string, new_object_id: string | null, mesh_center_point: Vector3, mesh_size: Vector3, floors: any, handleAsync: (meshLogo: { storeLogo: Object3D }) => void): void => {
    const img = getImage(map_obj, allIndexedRetailers[map_obj.retailer_id]);

    if (img === null) return;

    processImage(img, map_obj, (geometry, material) => {
      const logoMesh = new Mesh(geometry, material) as IExtMesh;

      // @ts-ignore
      const floor_index = mesh.floor_index;
      layer_text_logo_position_by_id(object_id, mesh_center_point, mesh_size, logoMesh, allIndexedMapObjects);
      logoMesh.object_id = new_object_id;
      logoMesh.floor_index = floor_index;
      logoMesh.renderOrder = getRenderOrder('layer-image');
      floors[floor_index]?.objsGroup.add(logoMesh);
      handleAsync({ storeLogo: logoMesh });
    })

  }, [getRenderOrder])

  const get_store_name_logo_geometry = useCallback((
    geometry: BufferGeometry,
    object_id: string,
    floor_index: number,
    textLogoNamePrefix: string,
    myFont: any,
    handleAsync: (meshLogo: any) => void
  ): { textMesh: IExtMesh } | null | false => {
    const new_object_id = textLogoNamePrefix + object_id;
    // deleteMeshByObjectID(new_object_id, scene, floors);
    let result = null;

    if (!geometry.boundingBox) {
      geometry.computeBoundingBox();
    }
    if (!geometry.boundingBox) {
      return null;
    }

    const boundingBox = geometry.boundingBox;

    const mesh_center_point = new Vector3();
    boundingBox.getCenter(mesh_center_point);

    const mesh_size = new Vector3();
    boundingBox.getSize(mesh_size);

    //@ts-ignore
    let map_obj: MapObj = allIndexedMapObjects[object_id] ? allIndexedMapObjects[object_id] : { layer_type: object_id.startsWith('escalator') ? 'escalator' : 'custom_text', 'custom_text': '', 'size': 8 };

    if (['retail_name', 'retail_text', 'custom_text'].includes(map_obj.layer_type)) {
      let text = '';
      if (map_obj.layer_type === 'retail_name' && map_obj.retailer_id && allIndexedRetailers[map_obj.retailer_id]) {
        text = allIndexedRetailers[map_obj.retailer_id].retail_name;
      } else if (['retail_text', 'custom_text'].includes(map_obj.layer_type)) {
        text = map_obj.custom_text;
      } else {
        text = '';
      }

      let text_color: Color = mapConfig.STORE_TEXT_COLOR;
      if (map_obj.text_color) {
        text_color = hex_to_color(map_obj.text_color);
      }

      const material_settings = {
        color: text_color,
        transparent: false,
        side: DoubleSide,
        depthWrite: false,
        depthTest: false,
      };
      let text_material = new MeshBasicMaterial(material_settings);
      let text_geometry = new TextGeometry(text, {
        font: myFont,
        size: parseInt(map_obj.size),
        height: 0.01,
        curveSegments: 4,
      });
      text_geometry.center();

      const textMesh = new Mesh(text_geometry, text_material) as IExtMesh;
      textMesh.userData.debug_name = text;
      textMesh.userData.font = myFont;
      layer_text_logo_position_by_id(object_id, mesh_center_point, mesh_size, textMesh, allIndexedMapObjects);
      textMesh.object_id = new_object_id;
      textMesh.floor_index = floor_index;
      textMesh.renderOrder = getRenderOrder('layer-text');
      if (map_obj.layer_type === 'retail_name') {
        textMesh.userData.retail_name = text;
      }

      floorsData[floor_index].objsGroup.add(textMesh);
      result = { textMesh };
    } else if (
      map_obj.layer_type === 'retail_logo' ||
      map_obj.layer_type === 'custom_image' ||
      (map_obj.obj_type === 'special' && map_obj.layer_type === 'kiosk' && map_obj.kiosk_id != null) ||
      (map_obj.obj_type === 'special' && map_obj.layer_type === 'amenity' && map_obj.value !== '') ||
      (map_obj.layer_type === 'escalator')
    ) {
      getImageLogo(allIndexedMapObjects, allIndexedRetailers, map_obj, geometry, object_id, new_object_id, mesh_center_point, mesh_size, floorsData, handleAsync);
    }
    return result;
  }, [allIndexedMapObjects, allIndexedRetailers, floorsData, getImageLogo, getRenderOrder, mapConfig.STORE_TEXT_COLOR])

  return {
    drawTextLogoStoreOnMap,
    addTextOrLogoOnStore,
    getMeshGroupBoundingBox,
    layer_text_logo_position_by_id,
    getImage,
    processImage,
    getImageLogo,
    get_store_name_logo_geometry
  }
}

export default useDrawLogoHelper;